import {Component, OnInit,ElementRef,EventEmitter ,ViewChild } from '@angular/core';
import { animate, AnimationEntryMetadata, state, style, transition, trigger } from '@angular/core';
import { DomSanitizationService, SafeHtml } from '@angular/platform-browser';
import {HunkImgComponent } from '../../components/hunk-img/hunk-img.component';

@Component({
    selector: 'hunk-richtext',
    moduleId: module.id,
    templateUrl: 'richtextview.html',
    providers: [],
    styleUrls: ["richtextview.css"],
    directives: [HunkImgComponent],
    inputs: ['htmlcontent', 'saveIconBarTop'],
    outputs: ['saved']


})
export class HunkRichTextComponent implements OnInit {

    @ViewChild('editorContent') editorContent:ElementRef;
    saveIconBarTop = 280;
    htmlcontent:string;
    source:string;
    isEditing = false; //editing mode
    viewIconsIconsVisible = false;
    saved = new EventEmitter<string>();
    focuseelement:any; //szerkeszthető div
    showSource:boolean = false;
    mainstyle = {padding: 50, lineheight: 99, width: 500, top: 0};
    fontColors = ["AliceBlue", "AntiqueWhite", "Aqua", "Aquamarine", "Azure", "Beige", "Bisque", "Black", "BlanchedAlmond", "Blue", "BlueViolet", "Brown", "BurlyWood", "CadetBlue", "Chartreuse", "Chocolate", "Coral", "CornflowerBlue", "Cornsilk", "Crimson", "Cyan", "DarkBlue", "DarkCyan", "DarkGoldenRod", "DarkGray", "DarkGrey", "DarkGreen", "DarkKhaki", "DarkMagenta", "DarkOliveGreen", "Darkorange", "DarkOrchid", "DarkRed", "DarkSalmon", "DarkSeaGreen", "DarkSlateBlue", "DarkSlateGray", "DarkSlateGrey", "DarkTurquoise", "DarkViolet", "DeepPink", "DeepSkyBlue", "DimGray", "DimGrey", "DodgerBlue", "FireBrick", "FloralWhite", "ForestGreen", "Fuchsia", "Gainsboro", "GhostWhite", "Gold", "GoldenRod", "Gray", "Grey", "Green", "GreenYellow", "HoneyDew", "HotPink", "IndianRed", "Indigo", "Ivory", "Khaki", "Lavender", "LavenderBlush", "LawnGreen", "LemonChiffon", "LightBlue", "LightCoral", "LightCyan", "LightGoldenRodYellow", "LightGray", "LightGrey", "LightGreen", "LightPink", "LightSalmon", "LightSeaGreen", "LightSkyBlue", "LightSlateGray", "LightSlateGrey", "LightSteelBlue", "LightYellow", "Lime", "LimeGreen", "Linen", "Magenta", "Maroon", "MediumAquaMarine", "MediumBlue", "MediumOrchid", "MediumPurple", "MediumSeaGreen", "MediumSlateBlue", "MediumSpringGreen", "MediumTurquoise", "MediumVioletRed", "MidnightBlue", "MintCream", "MistyRose", "Moccasin", "NavajoWhite", "Navy", "OldLace", "Olive", "OliveDrab", "Orange", "OrangeRed", "Orchid", "PaleGoldenRod", "PaleGreen", "PaleTurquoise", "PaleVioletRed", "PapayaWhip", "PeachPuff", "Peru", "Pink", "Plum", "PowderBlue", "Purple", "Red", "RosyBrown", "RoyalBlue", "SaddleBrown", "Salmon", "SandyBrown", "SeaGreen", "SeaShell", "Sienna", "Silver", "SkyBlue", "SlateBlue", "SlateGray", "SlateGrey", "Snow", "SpringGreen", "SteelBlue", "Tan", "Teal", "Thistle", "Tomato", "Turquoise", "Violet", "Wheat", "White", "WhiteSmoke", "Yellow", "YellowGreen"];

    fontSizes = [];
    customClasses = [{'name': 'piros fejléccel'}, {'name': 'Főcím'}]

    constructor(private _element:ElementRef, private domSanitizationService:DomSanitizationService) {


    }

    ngOnInit() {
        // console.log(this.source);
        // this.refresh();
        for (let i = 8; i < 51; i = i + 2) {
            this.fontSizes.push(i);
        }
        console.log(this._element);
        this.mainstyle.width = this._element.nativeElement.children[0].clientWidth;
        this.mainstyle.top = this._element.nativeElement.children[0].offsetTop;

    }

    /*viewIconsIcons events BEGIN*/
    onShowSource() {
        this.showSource = !this.showSource;
        if (this.showSource) {
            if (this.source) {

                this.refresh();
                this.source = this.htmlcontent;
            }

            else {


                this.source = this.htmlcontent;
            }

        }


    }

    editing(event) {

        if (this.source)
            this.htmlcontent = this.source;
        this.isEditing = event;

    }

    /*viewIconsIcons events END*/
    private refresh() {
        //  console.log(this.editorContent);
        if (this.viewIconsIconsVisible)
            this.focuseelement = this.editorContent.nativeElement.innerHTML;

        else {
            this.focuseelement = this.editorContent.nativeElement.innerHTML;

        }
        this.source = this.focuseelement;

    }

    getSelectionHtml() {

        var range = window.getSelection().getRangeAt(0),
            content = range.extractContents(),
            span = document.createElement('SPAN');

        span.appendChild(content);
        var htmlContent = span.innerHTML;


        console.log(htmlContent);
        return htmlContent;
    }

    insertAction(tagBefore, tagAfter, object) {

        let sel = window.getSelection();

        /*console.log(sel.focusNode.parentNode.style);
         console.log(sel);*/
        let html:any;
        if (sel.toString() == "") {
            html = tagBefore + '&zwnj;' + tagAfter;
        } else {
            html = tagBefore + this.getSelectionHtml() + tagAfter;
        }

        let range = window.getSelection().getRangeAt(0);
        range.deleteContents();

        if (range.createContextualFragment) {
            let fragment = range.createContextualFragment(html);
            range.insertNode(fragment);
            this.refresh();

            if (sel.toString() == "")
                this.putFocusInsideTag(object);
        }


    }

    putFocusInsideTag(object) {

        var el = window.getSelection().focusNode.nextSibling;
        var range = document.createRange();
        var sel = window.getSelection();
        if (!object)
             range.setStart(el, 1);
        range.collapse(true);
        sel.removeAllRanges();
        sel.addRange(range);
    }

    updateFromSource() {
        this.htmlcontent = this.source;

    }

    save() {
        this.refresh();
        // console.log(this.source);
        this.saved.emit(this.source);
    }

    /*Gloabals Editing Begin*/
    changeFontSize(fontsize) {

        this.insertAction('<span style=font-size:' + fontsize + 'px;>', '</span>',true);
        //   this.changelineSize(fontsize*1.4);
    }

    changeFontColor(color) {

        this.insertAction('<span style=color:' + color + '>', '</span>',false);
    }

    changebgColor(color) {
        this.insertAction('<span style=background-color:' + color + '>', '</span>',false);
    }

    changelineSize(fontsize) {

        this.insertAction('<span style=line-height:' + fontsize + 'px>', '</span>',false);
    }

    onChangeMainStyle(way) {
        switch (way) {
            case 'compress':
            case 'expand':
                this.mainstyle.padding += (way == 'compress') ? +20 : -20;
                break;

        }

    }

    /*Gloabals Editing END*/
    getHtml() {
        /*   return this.domSanitizationService.bypassSecurityTrustHtml(`<div class="dynamic"><b><span style="font-size: 40px">Hel</span>lo</b>> world</div>`);*/
        let html = this.domSanitizationService.bypassSecurityTrustHtml(this.htmlcontent);
        return html;
    }

    insertImg() {
        this.insertAction(
          /*  '<img src="template/images/eredmenyhirdetes/02.jpg" > ', '',true*/
              '<app-hunk-img" >', '</app-hunk-img>',true
        );

    }

    teszt() {
        alert('csoda');
    }
}

